body {
  font-size: 14px;
}

#appGlobalGrid {
  grid-template-columns: 70fr 30fr;
}

.appGlobalHeaderContainer {
  grid-template-columns: 10fr 80fr 10fr;
  grid-template-rows: 50% 50%;
}

.appGlobalHeaderContainerData {
  grid-template-columns: 1px 100fr;
  grid-column: 2;
  grid-row: 2;
}

.appGlobalHeaderContainerImage {
  grid-column: 2;
  grid-row: 1;
  display: grid;
  grid-template: auto calc(43vw + 35px) auto/auto calc(43vw + 35px) auto;
}

.appGlobalHeaderContainerImage div:nth-child(2) {
  display: grid;
  grid-template: auto calc(43vw + 20px) auto/auto calc(43vw + 20px) auto;
}

.appGlobalContentMain {
  font-size: 14px;
}

.appContentSkills {
  display: grid;
  grid-template-columns: repeat(5, auto);
}

.extended .appGlobalHeaderMainContainer {
  height: 110vw;
}

.skillContainer:nth-child(1) {
  grid-column: 1;
}

.skillContainer:nth-child(2) {
  grid-column: 2;
}

.skillContainer:nth-child(3) {
  grid-column: 3;
}

.skillContainer:nth-child(4) {
  grid-column: 4;
}

.skillContainer:nth-child(5) {
  grid-column: 5;
}

.skillsDetails {
  grid-column: 1/span 5;
}